@import url(http://fonts.googleapis.com/css?family=PT+Serif);
@import url(http://fonts.googleapis.com/css?family=Cantarell);

$base-font-family: sans-serif;
$content-font-family: sans-serif;
$text-color: #525151;
$header-color: #3c3b3b;
$theme-color: #34215A;
$action-color: #4CB894;
$block-background-color: #fdfafa;
$header-background-color: rgba(255, 255, 255, 0.88);
$header-shadow-color: #636262;
$image-caption-color: #8b8888;
$block-quote-color: #8b8888;
$footer-background-color: #f5f5f5;
$footer-background-shadow: #E0DBDB;
$footer-text-color: #939291;
$button-border: #eee;
$button-background: rgba(247, 247, 247, 0.44);
$more-button-color: #979797;

/**
 * Reset some basic elements
 */
body, h1, h2, h3, h4, h5, h6,
p, blockquote, pre, hr,
dl, dd, ol, ul, figure, a {
    margin: 0;
    padding: 0;
}

::selection {
  background: #DDD9E4; /* WebKit/Blink Browsers */
}
::-moz-selection {
  background: #DDD9E4; /* Gecko Browsers */
}


/**
 * Basic styling
 */
body {
    font-family: $base-font-family;
    font-size: $base-font-size;
    line-height: $base-line-height;
    font-weight: 400;
    color: $text-color;
    -webkit-text-size-adjust: 100%;
    min-width: 320px;

    /**
     * Links
     */
    a {
        color: $theme-color;
        text-decoration: none;

        &:hover {
          color: inherit;
          text-decoration: none;
        }
    }
}



/**
 * Set `margin-bottom` to maintain vertical rhythm
 */
h1, h2, h3, h4, h5, h6,
p, blockquote, pre,
ul, ol, dl, figure,
%vertical-rhythm {
    margin-bottom: $spacing-unit / 2;
}



/**
 * Images
 */
img {
    display: block;
    max-width: 100%;
    vertical-align: middle;
    margin-left: auto;
    margin-right: auto;
}

//p, pre {
//    margin-left: 20px;
//}


/**
 * Figures
 */
figure > img {
    display: block;
}

figcaption {
    font-size: $small-font-size;
}


/**
 * Lists
 */
ul, ol {
    margin-left: $spacing-unit;
}

li {
    > ul,
    > ol {
         margin-bottom: 0;
    }
}



/**
 * Headings
 */
h1, h2, h3 h4 {
    font-weight: bold;
    color: #525151;
}

h5, h6 {
    font-weight: 300;
}

h2, h3, h4 {
    margin-top: 30px !important;
}

/**
 * paragraph
 */
p {
  font-size: 15px;
  color: #525151;
  line-height: 1.8;
}



/**
 * Blockquotes
 */
blockquote {
    background-color: $block-background-color;
    border-left: 4px solid #eee;
    padding-left: $spacing-unit / 10;
    font-weight: 300;
    font-size: 14px !important;
    color: $block-quote-color !important;

    p {
      font-size: 14px;
      color: $block-quote-color;
    }

    > :last-child {
        margin-bottom: 0;
    }
}



/**
 * Code formatting
 */
pre,
code {
    font-size: 90%;
}

code {
    padding: 1px 5px;
    font-family: Consolas, "Liberation Mono", Menlo, Courier, monospace;
}

pre {
    padding: 8px 12px;
    white-space: pre-wrap;
    white-space: -moz-pre-wrap;
    white-space: -pre-wrap;
    white-space: -o-pre-wrap;
    word-wrap: break-word;

    > code {
        border: 0;
        padding-right: 0;
        padding-left: 0;
    }
}



/**
 * Wrapper
 */
.wrapper {
    max-width: -webkit-calc(1000px - (#{$spacing-unit} * 2));
    max-width:         calc(1000px - (#{$spacing-unit} * 2));
    margin-right: auto;
    margin-left: auto;
    padding-right: $spacing-unit;
    padding-left: $spacing-unit;
    @extend %clearfix;

    @include media-query($on-laptop) {
        max-width: -webkit-calc(1000px - (#{$spacing-unit}));
        max-width:         calc(1000px - (#{$spacing-unit}));
        padding-right: $spacing-unit / 2;
        padding-left: $spacing-unit / 2;
    }
}




/**
 * Clearfix
 */
%clearfix {
    &:after {
        content: "";
        display: table;
        clear: both;
    }
}



/**
 * Icons
 */
.icon {
    > svg {
        display: inline-block;
        width: 16px;
        height: 16px;
        vertical-align: middle;

        path {
            fill: $grey-color;
        }
    }
}
